body {
  margin: 0;
}

:root {
  --hover-color: #00acdc;
  --header-height: 60px;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
}

.conteiner {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_list:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:hover, .is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

